import React,{useState} from 'react';
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

function App() {
  const estadoInicial ={
    dni:'',
    respuesta:false,
    prestamos:[],
    cliente:[]
  }
  const [state, setstate] = useState(estadoInicial)
  const cambioEstado=(name,value)=>{
      setstate({...state,[name]:value})
  }
  const reiniciarEstado = ()=>{
      setstate(estadoInicial)
  }
  const consumirApi=(valor)=>{
      if (valor) {
          const URL_API="https://mihbapi.herokuapp.com/api/prestamoscliente?dniCliente="
          fetch(URL_API+`${valor}`)
          .then(res => res.json())
          .then(data => {
              setstate({
                  dni:valor,
                  respuesta:true,
                  prestamos:data[0],
                  cliente:data[1]
              })
          })
          .catch((e) =>  {
              console.log(e)
              alert("Ingresa el número de DNI correcto")
          })
      }else{
          alert("Ingresa el número de DNI")
      }
  }
  if (state.respuesta === false) {
      return (
        <div className="col-12 px-4">
          <div className="form-group mb-3">
            <label htmlFor="dni" className="text-secondary" >Dni del Cliente</label>
            <input type="number" className="form-control mt-1" name="dniCliente" id="dni" onChange={(e) => cambioEstado('dni',e.target.value)} placeholder="Introduce tu DNI"/>
          </div>
          <button type="button" name="" id="" onClick={()=>consumirApi(state.dni)}  className="btn btn-info text-light col-12" >Comprobar</button>
        </div>
      )
  }else if (state.respuesta === true) {
      return (
          <div className="col-12 px-4">
            <div className="row">
              <div className="col-12 px-3 my-3">
                  <p>
                      Bienvenido,<span className="h5 text-success"> {state.cliente.nombreCliente}</span> , a nuestra aplicacion de comprobacion de prestamo.
                  </p>
              </div>
              <div className="col-12 px-3 mb-3">
                  <p className="h3 text-info border-2 border-bottom border-info pb-2">
                      Listado de Prestamos
                  </p>
              </div>
              {state.prestamos.map((i,key)=>{
                  return (
                      <div key={key} className={`
                      col-12 my-2 shadow-sm rounded-3 p-2 card 
                      ${(i.estadoPrestamo==="pendiente")?"pendiente":""}
                      ${(i.estadoPrestamo==="aprobado")?"aprobado":""}
                      ${(i.estadoPrestamo==="rechazado")?"rechazado":""}
                      `}>
                          <div className="card-body">
                            <p className="card-title h5">Prestamo N° {key+1}: </p>
                            <p className="card-subtitle">Monto:
                              <span className="text-success h6"> $ {i.montoPrestamo}</span> 
                            </p>
                            <p className="card-subtitle">Numero de Cuotas:
                              <span className="text-success h6"> {i.cuotasPrestamo}</span> 
                            </p>
                            <p className="card-subtitle">Estado:
                              <span className="text-success h6"> {i.estadoPrestamo}</span> 
                            </p>
                          </div>
                      </div>
                      )
                  })
              }
              <button type="button" name="" id="" onClick={()=>{reiniciarEstado()}} className="btn btn-info text-light" >Volver</button>
            </div>
          </div>
      )
  } 
}

export default App;
